@use '../../variables.scss' as v;
@use '../../mixins/buttonReset.scss' as b;
@use '../../mixins/adaptiveFonts.scss' as f;
@use '../../mixins/listReset.scss' as l;

.nav-city {
  position: relative;
  height: 100%;

  @media (max-width: 450px) {
    width: 100%;
  }

  & .title {
    margin-bottom: 15px;
    font-family: v.$mainFontBold;
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    text-transform: uppercase;
  }

  &__title {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    @include b.buttonReset();

    padding: var(--nav-btn-pd);
    font-family: inherit;
    font-weight: 400;
    font-size: 14px;

    height: 100%;

    color: v.$primary-color;
    background-color: v.$blue-color;
    box-shadow: v.$boxShadow;
    border-radius: v.$borderRadius;
    transition: v.$transition-base;
    line-height: normal;

    text-decoration: none;
    cursor: pointer;


    @include f.fluidFontSize(14px, 20px, 375px, 1280px);

    @media (max-width: 450px) {
      width: 100%;

      justify-content: space-between;
    }

    @media (min-width: v.$xl) {
      line-height: 130%;
    }

    @media (min-width: v.$lg) {
      margin-left: 0;
    }

    &:focus,
    &:hover {
      color: v.$primary-color;
      background-color: v.$yellow-color;

      & svg {
        fill: v.$primary-color;
        stroke: v.$white-color;
      }
    }

    & ~ .nav-city__list.d-block {
      background-color: v.$yellow-color;
    }

    & svg {
      margin-left: 0.5rem;
    }

    & span {
      white-space: nowrap;
      // max-width: 94px;
      overflow: hidden;

      @media (min-width: 375px) {
        // max-width: 120px;
      }
      @media (min-width: v.$sm) {
        max-width: 120px;
      }
      @media (min-width: v.$xxl) {
        max-width: 200px;
      }
    }
  }

  &__wrapper {
    z-index: 10;
    position: absolute;
    top: 75%;
    left: 0;

    padding: 1rem;
    padding-right: 0.2rem;
    border-radius: v.$borderRadius;
    background-color: v.$yellow-color;

    padding-bottom: 0.1rem;
  }

  &__list {
    @include l.listReset();

    & li {
      min-width: 180px;
      margin-bottom: 0.25rem;

      @media (min-width: 375px) {
        min-width: 200px;
      }
      @media (min-width: v.$sm) {
        min-width: 175px;
      }
      @media (min-width: v.$xl) {
        min-width: 200px;
      }

      &:first-child a {
        font-weight: 700;
      }
    }

    & a {
      display: block;
      font-family: v.$mainFont;
      text-decoration: none;
      color: v.$primary-color;
      transition: v.$transition-base;

      font-weight: 400;
      font-size: 18px;
      line-height: 130%;

      padding-bottom: 0.1rem;

      @media (min-width: 375px) {
        font-size: 20px;
      }

      @media (min-width: v.$sm) {
        font-size: 17px;
      }
      @media (min-width: v.$xl) {
        font-size: 20px;
      }

      &:focus,
      &:hover {
        font-family: v.$mainFontBold;
        font-weight: 700;
        transition: v.$transition-base;
      }
    }
  }
}

.my-custom-scrollbar {
  height: 300px;
  overflow-y: auto;
  overflow-x: hidden;

  @media (min-width: v.$md) {
    height: auto;
  }

  /* Scrollbar */
  &::-webkit-scrollbar {
    width: 0.45rem;
  }
  &::-webkit-scrollbar-thumb,
  &::-webkit-scrollbar-track-piece {
    background: rgba(66, 71, 77, 0.15);
    border-radius: 41px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }
}
