@use '../variables.scss' as v;

.regions-promo {
  position: relative;
  padding-top: 30px;
  margin-bottom: 30px;

  @media (min-width: 567px) {
    padding-top: 45px;
    margin-bottom: 45px;
  }

  @media (min-width: 960px) {
    padding-top: 40px;
    margin-bottom: 60px;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 10%;
      width: 400px;
      height: 100%;
      background-image: url('../images/regions-fh/man.png');
      background-position: 100% 101%;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  @media (min-width: 1100px) {

    &::before {
      right: 17%;
    }
  }

  &__wrapper {
    padding: 20px 20px 120px;
    background-color: #42474d;
    color: white;
    border-radius: 22px;
    overflow: hidden;
    background-repeat: no-repeat;
    background-image: url('../images/regions-fh/cities.svg');
    background-position: 100% 100%;
    background-size: 100%;

    @media (min-width: 567px) {
      padding: 40px;
      background-size: 60%;
      background-position: 100% 105%;
    }

    @media (min-width: 700px) {
      background-size: 50%;
    }

    @media (min-width: 960px) {
      padding: 45px 70px;
      background-position: 100% 105%;
      background-size: 45%;
    }
  }

  &__text {
    position: relative;
    max-width: 620px;
  }

  &__title {
    font-family: v.$mainFontBold;
    margin-bottom: 1rem;
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    letter-spacing: 0.02em;

    @media (min-width: 567px) {
      font-size: 25px;
      line-height: 32.5px;
    }

    @media (min-width: 960px) {
      font-size: 30px;
      line-height: 39px;
    }

    & span {
      opacity: 0.5;

      @media (max-width: 567px) {
        display: block;
        margin-top: 0.5rem;
        max-width: 215px;
      }
    }
  }

  &__email {
    margin-bottom: 30px;
    // color: #fffeda;

    font-size: 15px;
    line-height: 19.5px;

    @media (min-width: 567px) {
      font-size: 20px;
      line-height: 26px;
    }

    & a {
      font-family: v.$mainFontBold;
      color: #fffeda;
      font-weight: 700;
      text-decoration: none;
    }
  }
}
