@use '../../variables.scss' as v;
@use '../../mixins/listReset.scss' as l;
@use '../../mixins/adaptiveFonts.scss' as f;

.links {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  @media (min-width: v.$sm) {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 0;
  }

  &__list {
    @include l.listReset();

    font-family: v.$mainFontBold;
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    text-transform: uppercase;

    width: 100%;


    @include f.fluidFontSize(17px, 20px, 375px, 1280px);

    @media (min-width: v.$sm) {
      width: 50%;
    }

    @media (min-width: v.$xxl) {
      padding-top: 4px;
      padding-left: 50px;
    }

    & li {
      margin-bottom: 0.77rem;

      @media (min-width: v.$xs) {
        margin-bottom: 0.7rem;
      }

      & span {
        width: 86%;
        @media (min-width: v.$sm) {
          width: 80%;
        }
        @media (min-width: v.$md) {
          width: 90%;
        }
      }
    }

    & a {
      display: block;
    }

    & .icon {
      @media (min-width: v.$sm) {
        margin-right: 1rem;
      }
      @media (min-width: v.$md) {
        margin-right: 0.7rem;
      }
    }
  }
}
