@use '../../mixins/buttonReset.scss' as m;
@use '../../mixins/adaptiveFonts.scss' as f;
@use '../../variables.scss' as v;

.button {
  @include m.buttonReset();
  padding: 16px 20px;
  font-family: v.$mainFontBold;
  font-weight: 700;
  font-size: 16px;
  line-height: 130%;
  transition: v.$transition-base;
  cursor: pointer;

  @include f.fluidFontSize(16px, 20px, 375px, 1280px);

  &--primary {
    padding: 0.5rem 2rem;
    color: v.$white-color;
    border: 2px solid v.$primary-color;
    background-color: v.$primary-color;
    box-shadow: v.$boxShadowDark;
    border-radius: 16px;

    &:focus,
    &:hover {
      color: v.$primary-color;
      background-color: transparent;
    }
  }
  &--yellow {
    padding: 0.5rem 2rem;
    color: v.$primary-color;
    border: 2px solid v.$yellow-color;
    background-color: v.$yellow-color;
    box-shadow: v.$boxShadowDark;
    border-radius: 16px;

    &:focus,
    &:hover {
      color: v.$white-color;
      background-color: transparent;
      border-color: v.$yellow-color !important;
    }
  }

  &--red {
    padding: 0.5rem 2rem;
    color: v.$white-color;
    border: 2px solid v.$red-color;
    background-color: v.$red-color;
    box-shadow: v.$boxShadowDark;
    border-radius: 16px;

    &:focus,
    &:hover {
      color: v.$red-color;
      background-color: transparent;
    }
  }

  &--outline {
    color: v.$white-color;
    border: 2px solid v.$white-color;
    border-radius: 16px;
    background-color: transparent;

    &:focus,
    &:hover {
      color: v.$yellow-color;
      border-color: v.$yellow-color;
      background-color: transparent;
    }
  }
  &--yellow {
    color: v.$primary-color;
    border: 2px solid v.$yellow-color;
    border-radius: 16px;
    background-color: v.$yellow-color;

    &:focus,
    &:hover {
      border-color: v.$primary-color;
    }
  }
}
