@use '../../variables.scss' as v;
@use '../../mixins/buttonReset.scss' as b;

.modal-history {
  & .modal-dialog {
    @media (min-width: v.$sm) {
      max-width: 690px;
    }
    @media (min-width: v.$md) {
      max-width: 856px;
    }
  }
  & .modal-body {
    @media (min-width: v.$sm) {
      padding: 32px;
    }
  }

  & .modal-content {
    border-radius: 22px;
    padding-bottom: 157px;
    background-image: url('../images/modal/girl-swimming-pool-mob.jpg');
    background-repeat: no-repeat;
    background-position: 0 100%;
    background-size: 100% 157px;

    @media (min-width: v.$sm) {
      background-image: var(--pic);
      padding-bottom: 220px;
      background-size: 100% 220px;
    }
    & .btn-close {
      @include b.buttonReset();
      color: v.$primary-color;

      & .icon {
        fill: v.$primary-color;
        stroke: v.$white-color;

      }
    }
  }
}
