@use '../../variables.scss' as v;

.form-message-modal {
  padding: 1rem 0;

  @media (min-width: v.$xs) {
    padding: 3rem 2rem;
  }
  @media (min-width: v.$sm) {
    padding: 1rem;
  }

  &--bg {
    @media (min-width: v.$sm) {
      background-image: url('../images/modal/modal-result.jpg');
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: 45% 100%;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 90%;
    margin: 0 auto;
  }

  &__title {
    font-family: v.$mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    color: v.$primary-color;
    text-align: center;

    @media (min-width: v.$md) {
      text-align: left;
    }

    & span {
      display: block;
    }
  }
}
