@use '../variables.scss' as v;

.modal-region {
  & .title {
    font-family: v.$mainFontBold;
    font-size: 20px;
    line-height: 26px;
    font-weight: 700;
  }
  & .modal-dialog {
    max-width: 788px;
  }
  & .modal-content {
    padding: 20px;

    @media (min-width: v.$sm) {
      padding: 40px;
    }
    @media (min-width: v.$md) {
      padding: 60px 40px 40px;
    }
  }

  & .modal-header {
    padding: 0;
    margin-bottom: 1rem;

    @media (min-width: v.$sm) {
      width: 50%;
    }

    & .title {
      @media (max-width: v.$xs) {
        padding-top: 1.2rem;
        width: 100%;
        font-size: 1.05rem;
        line-height: normal;
      }
    }
  }

  & .modal-content::before {
    width: 49%;
  }

  & .modal-body {
    padding: 0;

    & > div {
      font-size: 20px;
      line-height: 26px;

      @media (max-width: v.$xs) {
        font-size: 1.05rem;
        line-height: normal;
      }
    }

    @media (min-width: v.$sm) {
      width: 46%;
    }

    & a {
      color: inherit;
      text-decoration: revert;
    }

    & .form {
      display: flex;
      flex-direction: column;
      gap: 10px;

      & label {
        padding: 0;
        margin: 0;
      }

      & .form-control {
        padding: 0.5rem 1.25rem;
        min-height: 3rem;
        line-height: normal;
       
      }
    }
  }

  & .btn-close {
    width: 26px;
    height: 26px;

    & .icon {
      width: 100%;
      height: 100%;
    }
  }
}
