@use '../../variables.scss' as v;
@use '../../mixins/listReset.scss' as l;

.list {
  @include l.listReset();

  font-weight: 400;
  font-size: 16px;
  line-height: 130%;

  & li {
    font-weight: inherit;
    margin-bottom: 0.5rem;
  }

  & a {
    text-decoration: none;

    &:focus,
    &:hover {
      color: v.$yellow-color;
    }
  }

  &--bold {
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;

    & li {
      font-weight: 700;
    }
  }
}

