@use '../variables.scss' as v;

.regions-header {
  position: relative;
  z-index: 1;
  & h1 {
    font-family: v.$mainFontBold;
    font-size: 27px;
    font-weight: 700;
    line-height: 35.1px;
    letter-spacing: 0.02em;

    color: #42474d;

    @media (min-width: 567px) {
      font-size: 50px;
      line-height: 65px;
    }
    @media (min-width: 960px) {
      font-size: 60px;
      line-height: 78px;
    }
  }

  & .text {
    margin: 0;
    max-width: 780px;

    font-size: 15px;
    line-height: 19.5px;

    @media (min-width: 567px) {
      font-size: 20px;
      line-height: 26px;
    }
  }

  & .link-text {
    margin-top: 1rem;
    margin-bottom: 0;
  }
}

.regions-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 30px;

  @media (min-width: 567px) {
    margin-bottom: 45px;
  }

  @media (min-width: 960px) {
    margin-bottom: 60px;
  }

  & .styled-block {
    width: 100%;

    @media (min-width: v.$md) {
      width: calc(50% - 15px);
    }
  }
}
