@use 'sass:math';
/**
 * Rem-converter mixin to convert px to rem
 *
 * @param {Number} $pxValue - Value in pixels to be converted to rems.
 * @param {String} $property - CSS property to which the conversion will be applied. Default is 'font-size'.
 * @param {Number} $base-font-size - Base font size in pixels. Default is 16px.
 */
@mixin px-to-rem($pxValue, $property: 'font-size', $base-font-size: 16) {
  #{$property}: #{$pxValue}px;
  #{$property}: #{math.div($pxValue, $base-font-size)}rem;
}


