@use '../variables.scss' as v;

.styled-block {
  width: 100%;
  padding: 20px;
  margin-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 20px;
  border-radius: v.$borderRadius;
  background-color: v.$white-color;
  font-family: v.$mainFont;
  color: #42474d;

  @media (min-width: 375px) {
    padding: 40px 30px;
  }

  @media (min-width: 567px) {
    padding: 40px 60px;
  }

  @media (min-width: 960px) {
    padding: 40px 30px;
    margin-bottom: 45px;
  }

  @media (min-width: 1200px) {
    padding: 40px 30px;
    margin-bottom: 65px;
  }

  &--yellow {
    background-color: v.$yellow-color;

    // @media (min-width: 567px) {
    //   padding: 30px 35px;
    // }
  }

  &--blue {
    background-color: v.$blue-color;

    // @media (min-width: 567px) {
    //   padding: 30px 35px;
    // }
  }

  &__grid {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

    @media (min-width: v.$xl) {
      padding: 0 30px;
    }

    & .list-icon {
      width: 100%;

      @media (min-width: v.$sm) {
        width: calc(50% - 10px);
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 14px;
    font-family: v.$mainFontBold;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;

    @media (min-width: 567px) {
      font-size: 25px;
      line-height: 32px;
    }

    @media (min-width: 960px) {
      margin-bottom: 20px;
      font-size: 30px;
      line-height: 39px;
    }

    & span {
      @media (max-width: 567px) {
        display: block;
      }
    }
  }

  & ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    row-gap: 20px;

    & li {
      display: flex;
      gap: 10px;
      padding: 0;

      font-size: 15px;
      line-height: 19.5px;

      &[data-bold='bold'] span {
        font-family: v.$mainFontBold;
        font-weight: 700;
      }

      @media (min-width: 567px) {
        font-size: 20px;
        line-height: normal;
      }

      // @media (min-width: 550px) {
      //   font-size: 16px;
      //   line-height: normal;
      // }

      // @media (min-width: 1200px) {
      //   font-size: 16px;
      //   line-height: 26px;
      // }

      @media (min-width: 1200px) {
        font-size: 20px;
        line-height: 26px;
      }
    }

    &[data-size='sm'] {
      & svg {
        width: 30px;
        height: auto;

        @media (min-width: 567px) {
          width: 30px;
        }

        @media (min-width: v.$md) {
          width: 35px;
        }
      }
    }

    &[data-size='md'] {
      & svg {
        width: 40px;
        height: auto;

        @media (min-width: 567px) {
          width: 60px;
        }
      }
    }

    &[data-grid='grid'] {
      display: flex;
      flex-wrap: wrap;

      & li {
        width: 100%;

        @media (min-width: 1130px) {
          width: 50%;
        }
      }
    }
  }
}
