@use '../../variables.scss' as v;
@use '../../mixins/listReset.scss' as l;
@use '../../mixins/adaptiveFonts.scss' as f;

.nav-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 1.5rem 0 1.1rem;
  padding-top: var(--nav-menu-pt);
  padding-bottom: calc(var(--nav-menu-pt) - var(--nav-menu-mb));

  @media (max-width: v.$xs) {
    padding-left: 16px;
    padding-right: 16px;
  }

  @media (min-width: v.$sm) {
    padding-top: var(--nav-menu-pt);
    padding-bottom: calc(var(--nav-menu-pt) - var(--nav-menu-mb));
  }

  @media (min-width: v.$xl) {
    align-items: start;
  }

  &.container {
    @media (min-width: v.$md) {
      margin-bottom: 15px;
    }
    @media (min-width: v.$xl) {
      margin-bottom: 20px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    @include l.listReset();

    @media (max-width: 450px) {
      justify-content: space-between;
    }

    @media (min-width: v.$md) {
      flex-wrap: nowrap;
      align-items: center;
    }
    @media (min-width: v.$lg) {
      flex-wrap: wrap;
    }
  }

  &__item {
    margin-bottom: var(--nav-menu-mb);

    @media (max-width: 450px) {
      flex-grow: 1;
      width: calc(50% - 0.65rem);
    }

    &:not(:last-child) {
      margin-right: 0.65rem;

      @media (min-width: v.$xs) {
        margin-right: 0.55rem;
      }

      @media (min-width: v.$xl) {
        margin-right: 1rem;
      }
      @media (min-width: v.$xxl) {
        margin-right: 1.65rem;
      }
    }

    &:nth-child(2) {
      width: 100%;
      // max-width: 300px;
      margin-right: 0;

      @media (min-width: 375px) {
        // max-width: 310px;
      }

      @media (max-width: 450px) {
        width: 100%;
        margin-right: 0;
      }

      @media (min-width: 450px) {
        margin-right: 0.65rem;
        max-width: 325px;
      }

      @media (min-width: v.$xs) {
        width: auto;
      }

      @media (min-width: v.$xl) {
        // margin-right: 1.75rem;
        margin-right: 1rem;
      }
      @media (min-width: v.$xxl) {
        margin-right: 1.65rem;
      }
    }

    &.order-1.order-md-3 {
      @media (max-width: 450px) {
        width: 25%;
        margin-right: 0;
      }
    }
    &.order-3 {
      @media (max-width: 450px) {
        width: 40%;
      }
    }

    @media (min-width: v.$lg) {
      margin-right: 28px;

      &--city {
        margin-right: 20px;
      }
    }
  }

  &__link {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    padding: var(--nav-btn-pd);
    font-family: inherit;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;

    color: v.$primary-color;
    background-color: v.$blue-color;
    box-shadow: v.$boxShadow;
    border-radius: v.$borderRadius;
    transition: v.$transition-base;

    text-decoration: none;

    @include f.fluidFontSize(14px, 20px, 375px, 1280px);

    @media (min-width: v.$xl) {
      line-height: 130%;
    }

    &:focus,
    &:hover {
      color: v.$primary-color;
      background-color: v.$yellow-color;
      text-decoration: none;

      & svg {
        fill: v.$primary-color;
        stroke: v.$white-color;
      }
    }

    & span {
      padding-right: 12px;
      width: calc(100% - 20px);
    }

    &--sale {
      & span {
        padding-right: 20px;
      }
    }

    &.active {
      background-color: v.$yellow-color;
      color: v.$primary-color;
    }

    @media (min-width: v.$lg) {
    }

    & svg {
      fill: v.$primary-color;
      stroke: v.$white-color;
      width: 20px;
      height: 20px;
      margin-left: auto;
    }
  }

  & .link-pay {
    margin-bottom: var(--nav-menu-mb);
  }
}
