@font-face {
  font-family: 'Avenir Next Cyr';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local('Avenir Next Cyr'),
    url('../fonts/AvenirNextCyr-Regular.woff2') format('woff2'),
    url('../fonts/AvenirNextCyr-Regular.woff') format('woff');
}

@font-face {
  font-family: 'Avenir Next Cyr Demi';
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local('Avenir Next Cyr Demi'),
    url('../fonts/AvenirNextCyr-Demi.woff2') format('woff2'),
    url('../fonts/AvenirNextCyr-Demi.woff') format('woff');
}

@font-face {
  font-family: 'Avenir Next Cyr Light';
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local('Avenir Next Cyr Light'),
    url('../fonts/AvenirNextCyr-Light.woff2') format('woff2'),
    url('../fonts/AvenirNextCyr-Light.woff') format('woff');
}
