@use '../../variables.scss' as v;
@use '../../mixins/buttonReset.scss' as b;

.modal {
  & .btn-close {
    @include b.buttonReset();
    position: absolute;
    top: 1rem;
    right: 1rem;
    z-index: 11;
    color: v.$yellow-color;
    opacity: 1;
    background: none;
    width: 30px;
    height: 30px;
    padding: 2px;

    outline: none;
    cursor: pointer;

    &:focus,
    &:hover {
      outline: none;
      box-shadow: none;
    }

    @media (min-width: v.$sm) {
      color: v.$yellow-color;
    }

    & .icon {
      width: 26px;
      height: 26px;
      fill: v.$yellow-color;
      stroke: v.$primary-color;

      @media (min-width: v.$sm) {
        fill: v.$yellow-color;
        stroke: v.$primary-color;
      }
    }
  }

  &--bg {
    & .modal-content {
      position: relative;
      background-color: v.$yellow-color;
      border-radius: v.$borderRadius;
      border: none;
      border: 1px solid rgba(0, 0, 0, 0.1);

      box-shadow: v.$boxShadowDark;
      overflow: hidden;

      @media (min-width: v.$sm) {
        padding: 1rem;
        min-height: 240px;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
          width: 45%;
          background-image: var(--pic);
          background-repeat: no-repeat;
          background-position: 100% 0;
          background-size: cover;
          background-color: v.$primary-color;
        }
      }
    }

    & .modal-header {
      border: none;
      font-family: v.$mainFont;
      font-weight: 700;

      @media (min-width: v.$md) {
        width: 50%;
      }
    }

    & .title {
      width: calc(100% - 30px);
    }

    & .btn-close {
      z-index: 10;
      position: absolute;
      top: 1rem;
      right: 1rem;
      @include b.buttonReset();
      opacity: 1;
      color: v.$primary-color;
      background: none;

      display: flex;
      overflow: hidden;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;

      @media (min-width: v.$sm) {
        color: v.$yellow-color;
        position: absolute;
        top: 2rem;
        right: 2rem;
      }

      & .icon {
        fill: v.$primary-color;
        stroke: v.$yellow-color;

        @media (min-width: v.$sm) {
          fill: v.$yellow-color;
          stroke: v.$primary-color;
        }
      }
    }

    & .modal-body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      padding: 1rem;
      font-family: v.$mainFont;

      font-weight: 400;
      font-size: 15px;
      line-height: 140%;
      color: v.$primary-color;

      @media (min-width: v.$md) {
        width: 50%;
        // justify-content: start;
        padding: 0 1rem;
      }

      & p {
        margin: 0;
        margin-bottom: 0.5rem;
      }

      & ul {
        margin: 0;
        margin-bottom: 1rem;
        padding-left: 0.6rem;
      }
    }
  }
  &--bg-md {
    & .modal-content {
      position: relative;
      background-color: v.$yellow-color;
      border-radius: v.$borderRadius;
      border: none;
      border: 1px solid rgba(0, 0, 0, 0.1);

      box-shadow: v.$boxShadowDark;
      overflow: hidden;

      @media (min-width: v.$md) {
        padding: 1rem;
        min-height: 240px;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
          width: 45%;
          background-image: var(--pic);
          background-repeat: no-repeat;
          background-position: 100% 0;
          background-size: cover;
          background-color: v.$primary-color;
        }
      }
    }

    & .modal-header {
      border: none;
      font-family: v.$mainFont;
      font-weight: 700;
    }

    & .title {
      width: calc(95% - 30px);
    }

    & .btn-close {
      z-index: 10;
      position: absolute;
      top: 1rem;
      right: 1rem;
      @include b.buttonReset();
      opacity: 1;
      color: v.$primary-color;
      background: none;

      display: flex;
      overflow: hidden;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;

      @media (min-width: v.$md) {
        color: v.$yellow-color;
        position: absolute;
        top: 2rem;
        right: 2rem;
      }

      & .icon {
        fill: v.$primary-color;
        stroke: v.$yellow-color;

        @media (min-width: v.$md) {
          fill: v.$yellow-color;
          stroke: v.$primary-color;
        }
      }
    }

    & .modal-body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      padding: 1rem;
      font-family: v.$mainFont;

      font-weight: 400;
      font-size: 15px;
      line-height: 140%;
      color: v.$primary-color;

      @media (min-width: v.$md) {
        width: 50%;
        // justify-content: start;
        padding: 0 1rem;
      }

      & p {
        margin: 0;
        margin-bottom: 0.5rem;
      }

      & ul {
        margin: 0;
        margin-bottom: 1rem;
        padding-left: 0.6rem;
      }
    }
  }
}
