@use '../../variables.scss' as v;


.form-message {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1060;
  display: none;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  outline: 0;



  &__wrapper {
    position: relative;
    width: auto;
    pointer-events: none;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: flex;
    flex-direction: column;
    justify-content: center;

    background-color: v.$yellow-color;
    border-radius: v.$borderRadius;
    padding: 1rem 0;
    max-width: 820px;
    width: 100%;

    @media (min-width: v.$xs) {
      padding: 3rem 2rem;
    }
    @media (min-width: v.$sm) {
      padding: 1rem;
    }

    &--bg {
      @media (min-width: v.$sm) {
        background-image: url(../img/forms/1.jpg);
        background-repeat: no-repeat;
        background-position: 100% 0;
        background-size: 45% 100%;
      }
    }
  }

  &__content {
    @media (min-width: v.$sm) {
      width: 50%;
    }
  }

  &__title {
    max-width: 280px;
    font-family: v.$mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    color: v.$primary-color;
    text-align: center;

    @media (min-width: v.$xs) {
      text-align: left;
    }

    & span {
      display: block;
    }
  }
}
