@use '../../variables.scss' as v;
@use '../../mixins/listReset.scss' as l;
@use '../../mixins/adaptiveFonts.scss' as f;

.history {
  &__header {
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    width: 90%;
    @media (min-width: v.$sm) {
      width: 100%;
    }
  }
  & .title {
    padding-top: 20px;
    margin-bottom: 20px;

    font-weight: 700;
    font-size: 40px;
    line-height: 130%;
    /* identical to box height, or 52px */

    letter-spacing: 0.02em;
    text-transform: uppercase;

    @include f.fluidFontSize(23px, 40px, 450px, 1440px);

    @media (min-width: v.$sm) {
      padding-top: 0;
      padding-left: 30px;
    }
  }
  & .icon {
    display: none;
    width: 130px;
    height: 22px;
    margin-left: 3%;

    @media (min-width: v.$sm) {
      display: flex;
    }
    @media (min-width: v.$md) {
      margin-left: 10%;
    }
  }
  &__list {
    position: relative;
    display: flex;
    flex-direction: column;

    @include l.listReset();

    padding-left: 15px;

    &::before {
      content: '';
      position: absolute;
      top: 15px;
      left: 0;
      width: 5px;
      height: 91.5%;
      background: linear-gradient(180deg, #5e879f 21.12%, #77c7cc 100%);
      border-radius: 18px;

      @media (min-width: 400px) {
        height: 91%;
      }
      @media (min-width: 448px) {
        height: 93%;
      }
      @media (min-width: 450px) {
        height: 92%;
      }
      @media (min-width: v.$sm) {
        height: 91%;
      }
      @media (min-width: v.$md) {
        height: 90.5%;
      }
      @media (min-width: v.$xl) {
        height: 90.5%;
      }
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: start;
    padding-bottom: 16px;
  }

  &__title {
    position: relative;
    margin-right: 12px;
    padding: 5px 15px;
    font-weight: 700;
    font-size: 27px;
    line-height: 120%;
    border-radius: 30px;
    background-color: var(--bgColor);
    color: #ffffff;

    @media (min-width: v.$sm) {
      margin-right: 0;
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: -20px;
      transform: translateY(-50%);
      width: 14px;
      height: 14px;
      border-radius: 50%;

      background-color: var(--bgColor);
    }
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: #000000;

    @media (min-width: v.$sm) {
      padding-left: 15px;
    }
  }
}
