@use '../../variables.scss' as v;

.link-icon {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  text-decoration: none;
  font-family: inherit;

  color: v.$primary-color;

  &:focus,
  &:hover {
    text-decoration: none;
    color: v.$primary-color;
  }

  &--more {
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    transition: v.$transition-base;

    &:focus,
    &:hover {
      & .icon {
        width: 40px;
        transform: translateX(6px);
        transition: v.$transition-base;
      }
    }

    & .icon {
      margin-top: 0.2rem;
      margin-left: 0.5rem;
      height: 8px;
      transition: v.$transition-base;
    }
  }

  &--bold {
    font-family: v.$mainFontBold;
  }

  &--lower {
    text-transform: lowercase;
  }
}
