@use '../../variables.scss' as v;
@use '../../mixins/adaptiveFonts.scss' as f;

.link-pay {
  // padding: var(--btn-padding);
  padding: 17px 19px;
  font-family: v.$mainFontBold;
  font-weight: 700;
  font-size: 16px;
  line-height: 130%;
  color: v.$white-color;
  border: 2px solid v.$red-color;
  background-color: v.$red-color;
  box-shadow: v.$boxShadow;
  border-radius: v.$borderRadius;
  transition: v.$transition-base;

  text-decoration: none;

  @include f.fluidFontSize(16px, 20px, 375px, 1280px);

  &:focus,
  &:hover {
    color: v.$red-color;
    background-color: transparent;
    text-decoration: none;
  }

  @media (min-width: v.$xl) {
    padding: 17px 25px;
  }
}
