@mixin fluidFontSize(
  $f-min,
  $f-max,
  $w-min,
  $w-max,
  $fallback: false
) {
  // Фиксированное значение для браузеров без поддержки calc()
  @if ($fallback == true) {
    font-size: #{$f-min};
  }

  // Расчетное значение с использованием функции calc() и clamp()
  font-size: calc(#{$f-min} + (#{$f-max} - #{$f-min}) * ((100vw - #{$w-min}) / (#{$w-max} - #{$w-min})));
}
