@use '../variables.scss' as v;
@use '../mixins/adaptiveFonts.scss' as f;

.content {
  color: v.$primary-color;
  font-family: v.$mainFont;
  font-weight: 400;
  font-size: 15px;
  line-height: 140%;
  margin-bottom: 30px;

  @media (min-width: v.$xxl) {
    margin-bottom: 3.3rem;
  }

  &__slider {
    margin-bottom: 30px;
    @media (min-width: v.$xxl) {
      margin-bottom: 3.3rem;
    }
  }

  & .slider .swiper-slide {
    // max-height: 300px;
    @media (min-width: v.$md) {
      min-height: 500px;
    }
  }

  & h1,
  & h2,
  & h3 {
    margin-bottom: 10px;
    font-family: v.$mainFontBold;
    font-weight: 700;
    font-size: 27px;
    line-height: 130%;
    /* identical to box height, or 78px */

    letter-spacing: 0.02em;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: rgba(73, 76, 79, 0.5);

    @media (min-width: v.$xs) {
      margin-bottom: 0.8rem;
      font-size: 50px;
      line-height: 65px;
    }

    @media (min-width: v.$xl) {
      font-size: 60px;
      line-height: 78px;
    }

    & span {
      display: block;

      @media (min-width: v.$sm) {
        display: inline-block;
      }
    }
  }

  & p {
    margin-bottom: 0;
    color: inherit;

    font-size: 13px;

    @media (min-width: v.$xs) {
      font-size: 15px;
    }
  }

  & a {
    color: v.$primary-color;
    text-decoration: underline;

    &:hover {
      color: v.$primary-color;
    }
  }

  &--promo {
    & p {
      font-size: 20px;
      line-height: 130%;
    }
  }
}
